<template>
  <div class="dslfp">
    <div>
      <el-input placeholder="请输入名称"
                class="add-to"
                size="small"
                v-model="input1"
                @keydown.enter.native="doAddRole">
        <template slot="prepend">角色名称</template>
      </el-input>
      <el-button type="primary"
                 size="small"
                 icon="el-icon-plus"
                 @click="doAddRole">添加</el-button>
    </div>
    <div>
      <el-input placeholder="请输入名称"
                class="add-to"
                size="small"
                v-model="filterGradeName">
        <template slot="prepend">角色名称</template>
      </el-input>
      <el-button type="primary"
                 size="small"
                 icon="el-icon-search"
                 @click="filterGradeSearch">搜索</el-button>
    </div>
    <div class="permissManaMain">
      <el-collapse v-model="activeName"
                   accordion
                   @change="change">
        <el-collapse-item :title="r.grade_name"
                          :name="r.id"
                          v-for="(r,index) in roles"
                          :key="index">
          <el-card class="box-card">
            <div slot="header"
                 class="clearfix">
              <span>可访问资源</span>
              <el-button style="float: right; padding: 3px 0; color: #ff0000"
                         type="text"
                         icon="el-icon-delete"
                         @click="doDeleteRole(r)"></el-button>
            </div>
            <div>
              <el-tree show-checkbox
                       :data="allMenus"
                       :props="defaultProps"
                       ref="tree"
                       :key="index"
                       :default-checked-keys="selectedMenus"
                       node-key="id"></el-tree>
              <div style="display:flex; justify-content: flex-end">
                <el-button size="mini"
                           @click="canaelUpdate">取消修改</el-button>
                <el-button size="mini"
                           type="primary"
                           @click="doUpdate(r.id, index)">确认修改</el-button>
              </div>
            </div>
          </el-card>
        </el-collapse-item>
      </el-collapse>
    </div>
    <div style="display: flex; justify-content: flex-end; margin-top:20px">
      <el-pagination background
                     layout="sizes, prev, pager, next, jumper, ->, total"
                     @current-change="currentChange"
                     @size-change="sizeChange"
                     :page-size="gradeSize"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
// import { getRequest } from '@/utils/api'

// import { response } from 'express'

export default {
  name: 'grade_list',
  data() {
    return {
      gradePage: 1,
      gradeSize: 10,
      total: 0,
      input1: '',
      filterGradeName: '',
      gradeName: '',
      roles: [],
      allMenus: [],
      defaultProps: {
        children: 'children',
        label: 'name',
      },
      selectedMenus: [],
      activeName: -1,
      filter: '',
    }
  },
  mounted() {
    this.initRoles()
  },
  methods: {
    // 设置页码数量
    sizeChange(currentSize) {
      this.gradeSize = currentSize
      this.initRoles()
    },
    // 页码
    currentChange(currenPage) {
      this.gradePage = currenPage
      this.initRoles()
    },
    // 删除角色
    doDeleteRole(role) {
      this.$confirm(
        '此操作将永久删除[' + role.grade_name + ']角色, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      )
        .then(() => {
          this.getRequest('/home/Grade/grade_delete/gradeid/' + role.id).then(
            (resp) => {
              if (resp.code == 200) {
                this.initRoles()
                this.$message.success(resp.success)
              } else {
                this.$message.error(resp.error)
              }
            }
          )
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
          })
        })
    },
    // 搜索角色
    filterGradeSearch() {
      this.gradePage = 1
      this.gradeSize = 10
      this.filter = '/gradeName/' + this.filterGradeName
      console.log(this.filter)
      this.initRoles()
    },
    // 添加角色
    doAddRole() {
      this.getRequest('/home/Grade/grade_add/gradeName/' + this.input1).then(
        (resp) => {
          console.log(resp)
          if (resp.code == 200) {
            this.$message.success(resp.success)
            this.initRoles()
            this.input1 = ''
          } else {
            this.$message.error(resp.error)
          }
        }
      )
    },
    canaelUpdate() {
      this.activeName = -1
    },
    doUpdate(rid, index) {
      let tree = this.$refs.tree[index]
      let selectedKeys = tree.getCheckedKeys(true)

      let menuGroup = ''
      selectedKeys.forEach((key) => {
        menuGroup += ',' + key
      })
      let url =
        '/home/Grade/grade_update/id/' +
        rid +
        '/menuGroup/' +
        menuGroup.replace(/^,+/, '').replace(/,+$/, '')
      this.putRequest(url).then((resp) => {
        if (resp.code == 200) {
          this.$message.success(resp.success)
          this.initRoles()
          this.activeName = -1
        } else {
          this.$message.error(resp.error)
        }
      })
    },
    change(rid) {
      if (rid) {
        this.initAllMenus(rid)
      }
    },
    initAllMenus(rid) {
      // 设置默认权限选中、
      // this.roles有所有的角色权限 通过循环和if判断那个和点击的rid相同，从而得到点击的角色权限
      this.roles.forEach((permiss) => {
        if (rid == permiss.id) {
          if (permiss.permission) {
            this.selectedMenus = permiss.permission.split(',')
          }
        }
      })

      this.getRequest('/home/Sys/sys_menu_list').then((resp) => {
        if (resp.code == 200) {
          this.allMenus = this.objToArray(resp.data)
        }
      })
    },
    initRoles() {
      let url =
        '/home/Grade/grade_list' +
        '/gradePage/' +
        this.gradePage +
        '/gradeSize/' +
        this.gradeSize +
        this.filter
      console.log(url)
      this.getRequest(url).then((resp) => {
        if (resp.code == 200) {
          this.roles = resp.data
          this.total = resp.count
        }
      })
    },
    // 对象转数组
    objToArray(array) {
      let arr = []
      for (let i in array) {
        arr.push(array[i])
        if (array[i].children && array[i].children instanceof Object) {
          let res = []
          for (let e in array[i].children) {
            res.push(array[i].children[e])
            if (
              array[i].children[e].children &&
              array[i].children[e].children instanceof Object
            ) {
              let threeres = []
              for (let y in array[i].children[e].children) {
                threeres.push(array[i].children[e].children[y])
              }
              array[i]['children'][e]['children'] = threeres
            }
          }
          array[i]['children'] = res
        }
      }
      return arr
    },
  },
}
</script>

<style>
.dslfp {
  padding: 15px;
  background-color: white;
  opacity: 0.95;
  border-radius: 6px;
}

.add-to {
  width: 700px;
  margin: 0px 10px 10px 0px;
}

.permissManaMain {
}
</style>


